body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main_div{
  width:100%;
  height: 100vh;
  background: rgb(96, 96, 204);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width: 25%;
  height: 80vh;
  background-color: black;
  box-shadow:  5px 5px 25px -5px rgb(0,  0, 0, 0.5);
  border-radius: 15px;
}
h1{
  color: white;
  background: transparent;
  background-color: rgb(96, 96, 204);
  margin-bottom: 10px;
  padding: 6px 0 2px 0;
  box-shadow: 5px 5px 15px -5px rgb(0,  0, 0, 0.3) ;
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid white;
  outline: none;
  color: white;
}
button{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color:rgb(96, 96, 204); 
  color: white;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow:  5px 5px 15px -5px rgb(0,  0, 0, 0.3);;

}
button:hover{
  background-color: brown;
}
ol{
  margin-top: 30px;
}
ol li{
 margin-top: 2px;

}
ol li{
  padding-left:  10px;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: white;
  text-transform: capitalize;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left; 
  align-items: center;
}
.todo_style .fa-times {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center; 
  align-items: center; 
  background-color:rgb(96, 96, 204);  
  border-radius: 50%;
  margin: 10 15px 0 35px;
  color: white;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.todo_style .fa-times:hover .list_style{
  color: brown;

}
.todo_style .fa-times:hover{
  background-color: brown;
}